import store from "@/store";
import router from '@/router';
import config from '@/config';
window.is_Manual_Update=false;//是否是刷新页面执行的socket断开
export default class SocketUtil {
    static ws;      //socket实例

    constructor (parameter) {
        this._init();
    }
    _init () {}

    //连接socket
    static async connectSocket (callback){
        let ws= window.ws =new WebSocket(config.socketUrl+`/${store.state.app.token}`);
        let timeId;
        ws.onopen = (event)=>{
            console.log("socket连接成功.");
            if(callback && typeof callback=='function'){
                callback();
            }
            timeId=setInterval(()=>{
                ws.send(JSON.stringify({cmd:"nothing"}));
            },5000);
        };
        ws.onmessage = (event)=>{
            //收到消息后，派发事件给各组件传递数据。
            let res=JSON.parse(event.data);
            // if(res.type=='handicapInformation'){console.log("handicapInformation" + event.data)};
            if(res.type){
                _vm.$eventBus.$emit(res.type+'_msg',res);
            }
            // console.log(res.type,event.data);
        };
        
        ws.onclose = (event)=>{
            console.log("socket已关闭.");
            clearInterval(timeId);
            //刷新断开不弹窗，息屏断开弹窗
            if(window.is_Manual_Update==false){
                // var conf=confirm(_vm.$t('socketDisconnect'));
                // if(conf==true){
                //     window.location.reload();
                // }else {
                // }
            }
        };
        SocketUtil.ws=ws;
    }
    //关闭socket
    static async close (callback){
        window.is_Manual_Update=true;
        SocketUtil.ws.close();
        if(callback && typeof callback=='function'){
            callback();
        }
    }
}