//返回颜色值
//展示颜色。红：由新价格-就价格>0，反之绿，平价灰。
export function getColor(val,newVal,oldVal){
    if(!newVal || !oldVal){
        return ''
    }
    if(newVal-oldVal>0){
        return 'red'
    }
    if(newVal-oldVal<0){
        return 'green'
    }
    return '';
}