/**
 * 根据时间返回过去或未来第几天的日历。
 * myDate:时间戳，selectDay:选择过去的多少天，isNext：是否未来的时间，默认过去
 * 示例：compareTime(new Date("2020-01-01"),30,true)
 * 返回值：2019-12-02
 */
 export function compareTime(myDate, selectDay, isNext) {
    var lw, myDate = new Date(myDate).getTime();
    if (isNext) {
        lw = new Date(myDate + 1000 * 60 * 60 * 24 * selectDay);
    } else {
        lw = new Date(myDate - 1000 * 60 * 60 * 24 * selectDay);
    }
    var lastY = lw.getFullYear();
    var lastM = lw.getMonth() + 1;
    var lastD = lw.getDate();
    var startdate = lastY + "-" + (lastM < 10 ? "0" + lastM : lastM) + "-" + (lastD < 10 ? "0" + lastD : lastD);
    return startdate;
}

//返回颜色值
//展示颜色。红：由新价格-就价格>0，反之绿，平价灰。
export function getColor(newVal,oldVal){
    if(!newVal || !oldVal){
        return ''
    }
    if(newVal-oldVal>0){
        return 'red'
    }
    if(newVal-oldVal<0){
        return 'green'
    }
    return '';
}